import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, Grid } from '@mui/material';
import { Container } from '@mui/system';




const createNewsCard = [1, 2, 3, 4, 5, 6, 7, 8]


export default function NewsCard() {
  return (
    <Container sx={{ marginTop: '2rem'}}>
      <Grid container  
      rowSpacing={{ xs: 1, sm: 2, md: 3, lg: 4 }} 
      columnSpacing={{ xs: 1, sm: 2, md: 3, lg: 4 }}
      justifyContent='center'>

        {createNewsCard.map((createNewsCard) => (
          <Grid item key={createNewsCard} xs={12} sm={6} md={4} lg={3} xl={2}  >
            <Card sx={{ maxWidth: 345, mx:'auto'}}>
              <CardActionArea>
                <CardMedia
                  component="img"
                  height="140"
                  image="https://img.manoramanews.com/content/dam/mm/mnews/news/kerala/images/2024/2/16/Mozha.jpg.image.260.136.jpg"
                  alt="news"
                />
                <CardContent>
                  <Typography gutterBottom variant="h6" component="div">
                     ?   ?...
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                           .    . ...
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}

      </Grid>
    </Container>
  );
}
